@import '../variables';
@mixin base-setion {
  height: auto;
  width: 100%;
  @include flex(center, center);
}
.section {
  @include base-setion;
  padding: 5rem 1.5rem;
  &__white {
    color: $blue-dark;
    background-color: #fff;
  }
  &__blue {
    color: #fff;
    background-color: #23286b;
  }
}
